.quiz {
  position: relative;
  height: 88%;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top-right-radius: 25px;
  border-top-left-radius: 25px;
  padding-left: 5px;
  padding-right: 5px;
}

.quizHeader {
  position: absolute;
  right: 0;
  left: 0;
  top: -40px;
  background: transparent;
  height: 40px;
  padding-left: 80px;
  color: white;
  font-weight: bold;
  font-size: 20px;
}
@media (max-width: 1350px) {
  .quizHeader {
    padding-left: calc(64px + 16 * (100vw - 320px) / 1030);
  }
}

.quizNumberWrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 15px;
}

