@import '../../mixins/adaptiveMixin';

.quiz {
  position: relative;
  height: 88%;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top-right-radius: 25px;
  border-top-left-radius: 25px;
  padding-left: 5px;
  padding-right: 5px;
}

.quizHeader {
  position: absolute;
  right: 0;
  left: 0;
  top: -40px;
  background: transparent;
  height: 40px;
 @include adaptive("padding-left", 80, 64, 1);
  color: white;
  font-weight: bold;
  font-size:  20px;
}

.quizNumberWrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 15px;
}